
.popup-content{
    width: 70% !important;
}
.popup-overlay {
    position: absolute !important;
}

.popup-content > div {
    overflow: auto !important;
    position: relative !important;
    border-radius: 100px;
}

.popup-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.popup-content p{
    font-size: 12px;
}
.popup-h4{
  
text-align: center;
}
.ph-box1 {
    width: 10%;
}
.ph-box2 h1{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.popup-h4::before{
    content: "";
    width: 150px;
    height: 1px;
    background-color: blue;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
}
.popup-h4::after{
    content: "";
    width: 150px;
    height: 1px;
    background-color: blue;
    display: inline-block;
    vertical-align: middle;
    
}
.popup-box1{
    display: flex;
    justify-content: space-between;
      align-items: center;  
    
}
.pb-btn1{
    background-color: black;
    color: white;
}
.pb-box2child2 {
    display: flex;
    flex-direction: column;
}

.popup-table{
    border: 1px solid black;
    width: 100%;

}
.popup-table td {
    border: 1px solid black;
    
}
.popup-table th{
    border: 1px solid black;
}

@media(max-width:768px){
    .ph-box1 {
        width: 30%;
    }
    .popup-content{
        width: 90% !important;
    }
    .popup-h4::before{
        content: "";
        width: 150px;
        height: 1px;
        background-color: blue;
        display:none;
        vertical-align: middle;
        margin-right: 10px;
    }
    .popup-h4::after{
        content: "";
        width: 150px;
        height: 1px;
        background-color: blue;
        display: none;
        vertical-align: middle;
        
    }
}